import Vue from 'vue'
import HelloWorld from '@/components/HelloWorld'
import Home from '@/components/home'
import Ad from '@/components/nav1/ad'
import Sj from '@/components/nav1/ad2'
import Login from '@/Login'

let routes = [
  {
    path: '/login',
    name: '登陆',
    component: Login,
    hidden: true
  },{
      path: '/',
      name: 'Home',
      component: Home,
      iconCls: 'el-icon-message',
      children: [
        {path: '/ad',component: Ad, name: '广告机'},
        {path: '/sj',component: Sj, name: '商家信息'}
      ]
    }, {
      path: '/',
      name: '',
      component: Home,
      iconCls: 'el-icon-message',
      leaf: true,
      children: [
        {path: '/HelloWorld',component: HelloWorld, name: 'HelloWorld'},
      ]
    }
  ]

export default routes;



