<template lang="html">
    <el-form :model="ruleForm2" :rules="rules2" ref="ruleForm2" label-position="left" label-width="0" class="demo-ruleForm login-container">
      <h3 class="title">系统登陆</h3>
      <el-form-item prop="account">
        <el-input type="text" v-model="ruleForm2.account" auto-complete="off" placeholder="账号"></el-input>
      </el-form-item>
      <el-form-item prop="checkPass">
        <el-input type="password" v-model="ruleForm2.checkPass" auto-complete="off" placeholder="密码"></el-input>
      </el-form-item>
      <el-checkbox v-model="checked" checked class="remember">记住密码</el-checkbox>
      <el-form-item style="width:100%">
        <el-button type="primary" style="width:100%;" @click.native.prevent="handleSubmit2" :loading="logining">登陆</el-button>
      </el-form-item>
    </el-form>
</template>

<script>
import util from '@/util/utils'

export default {
  data() {
    return {
      logining: false,
      ruleForm2: {
        account: 'zcgl',
        checkPass: ''//zcgl123456
      },
      rules2: {
        account: [
          { required: true, message: '请输入账号', trigger: 'blur' }
        ],
        checkPass: [
          { required: true, message: '请输入密码', trigger: 'blur' }
        ]
      },
      checked: true
    }
  },
  methods: {
    handleReset2() {
      this.$refs.ruleForm2.resetFields()
    },
    handleSubmit2(ev) {
      var _this = this;
      this.$refs.ruleForm2.validate((valid) => {
        if (valid) {
          Bmob.User.login(this.ruleForm2.account, this.ruleForm2.checkPass).then(res => {

            sessionStorage.setItem('user', JSON.stringify(this.ruleForm2))
            console.log(res);
            this.$router.push({path: '/ad'})
          }).catch(err => {
            this.$message('账号或者密码错误！')
          })
        }
      })
    }
  }
}
</script>

<style lang="css">
.login-container {
  border-radius: 5px;
  background-clip: padding-box;
  margin: 180px auto;
  width: 350px;
  padding: 35px 35px 15px;
  background-color: #fff;
  border:1px solid #eaeaea;
  box-shadow: 0 0 25px #cac6c6;
}
.login-container .remember {
  margin: 0 0 35px;
}
</style>



