<template>
  <div id="app">
    <transition name='fade' mode='out-in'>
      <router-view/>
    </transition>
  </div>
</template>

<script>
export default {
  name: 'App'
}
</script>

<style>
body {
  margin: 0;
  padding: 0;
  font-family: Helvetica Neue;
  -webkit-font-smoothing: antialiased;
  box-sizing: border-box;
}
#app {
  position: absolute;
  top: 0;
  bottom: 0;
  width: 100%;
}
.el-submenu[class^=fa],
.el-menu-item[class^=fa]  {
  vertical-align: baseline;
  margin-right: 10px;
}
.toolbar {
  background: #f2f2f2;
  padding: 10px;
  margin:10px 10px;
}
.toolbar .el-form-item {
  margin-bottom: 10px;
}

.fade-enter-active,
.fade-leave-active {
  transition: all .2s ease;
}

.fade-enter,
.fade-leave-active {
  opacity: 0;
}

</style>



