 <template lang="html">
  <section>
    <!--工具条-->
    <el-col :span="24" class="toolbar" style="padding-bottom:0">
      <el-form :inline="true" >
        <el-form-item>
          <el-input  placeholder="小区地址" v-model="selectValue"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="getSelect">查询</el-button>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="handleAdd">新增</el-button>
        </el-form-item>
      </el-form>
    </el-col>
    <!--列表-->
    <el-table :data="formData" style="width: 100%" class="my-table" v-loading="loading" highlight-current-row @expand-change="getAdList">
        <el-table-column type="expand">
          <template slot-scope="props">
            <div class="ad-lunbo-text" v-show=" props.row.ad_text != '' ">
              {{ props.row.ad_text }}
            </div>
            <el-table :data="adinfo[props.row.ad_id]" style="width:100%;" :border="true" v-loading="loadingExpand" >
              <el-table-column type="index"></el-table-column>
              <el-table-column prop="sjgd_name" label="商家名称"></el-table-column>
              <el-table-column prop="gd_name" label="广告名称">></el-table-column>
              <el-table-column prop="gd_time" label="广告时间(s)">></el-table-column>
              <el-table-column label="操作">
                <template scope="scope">
                  <!-- <el-button size="small" @click="adEdit(scope.$index, scope.row, props.row.id)">编辑</el-button> -->
                  <el-button type="danger" @click="gdDellet(scope.$index,props.row)" size="small" >删除</el-button>
                </template>
              </el-table-column>
            </el-table>
          </template>
        </el-table-column>
        <el-table-column prop="ad_area" width="240" label="投放小区"></el-table-column>
        <el-table-column prop="ad_area_list" label="单元号" ></el-table-column>
        <el-table-column prop="ad_class" label="所属物业" ></el-table-column>
        <el-table-column prop="ad_num" label="广告数量" ></el-table-column>
        <el-table-column label="操作" width="150">
          <template scope="scope">
            <!-- <el-button size="small" @click="handleEdit(scope.$index, scope.row)">编辑</el-button> -->
            <el-button type="primary" size="small" @click="addAd(scope.$index, scope.row)" >新增</el-button>
          </template>
        </el-table-column>
    </el-table>
    <!--工具条-->
    <el-col :span="24" class="toolbar" v-show="this.allData.length>10">
      <el-pagination layout="prev, pager, next" background  :page-zise="pageSize" :total="total"
      @size-change="sizeChange" @prev-click="prevClick" @next-click="nextClick" @current-change="currentChange"
      style="float:right">
      </el-pagination>
    </el-col>

    <!--新增广告机-->
    <el-dialog title="新增广告机" :visible.sync="addNew">
      <el-form :model="addNewList" label-width="80px" ref="addNewList">
          <el-form-item label="投放小区">
            <el-input v-model="addNewList.ad_area"></el-input>
          </el-form-item>
          <el-form-item label="单元号">
            <el-input v-model="addNewList.ad_area_list"></el-input>
          </el-form-item>
          <el-form-item label="所属物业">
            <el-input v-model="addNewList.ad_class"></el-input>
          </el-form-item>
          <el-form-item label="轮播文字">
            <el-input v-model="addNewList.ad_text"></el-input>
          </el-form-item>
          <!-- <el-form-item label="安装时间">
            <el-date-picker v-model="addNewList.ad_time" type="date" style="width:100%" ></el-date-picker>
          </el-form-item> -->
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click.native="addNew = false">取消</el-button>
          <el-button type="primary" @click="addNewAdList" >提交</el-button>
        </div>
    </el-dialog>

    <!--编辑广告机-->
    <!-- <el-dialog title="编辑广告机" :visible.sync="editFormVisable">
      <el-form :model="editForm" label-width="80px" ref="editForm">
        <el-form-item label="小区名称">
          <el-input v-model="editForm.ad_area" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="安装时间">
          <el-date-picker v-model="editForm.ad_time" type="date" ></el-date-picker>
        </el-form-item>
        <el-form-item label="轮播文字">
          <el-input type="textarea" v-model="editForm.ad_text" autosize></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click.native="editFormVisable = false">取消</el-button>
        <el-button type="primary" @click="editSubmit()" >提交</el-button>
      </div>
    </el-dialog> -->

    <!--新增广告-->
    <el-dialog title="新增广告" :visible.sync="adFormVisable">
      <el-form :model="gdForm" label-width="80px" ref="gdForm">
        <el-form-item label="商家名称">
          <el-input v-model="gdForm.sjgd_name" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="广告名称">
          <el-input v-model="gdForm.gd_name" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="广告时长">
          <el-input v-model="gdForm.gd_time" autosize></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click.native="editFormVisable = false">取消</el-button>
        <el-button type="primary" @click="AdeditSubmit" >提交</el-button>
      </div>
    </el-dialog>

  </section>
</template>

<script>

import util from '../../util/utils'
import '../../assets/js/key.js'
const adList = Bmob.Query('adList');
const gdList = Bmob.Query('gdlist');

export default {
  data() {
    return {
      formData: [],
      allData:[],
      adinfo:[],
      addNew: false,
      addNewList:{},
      total: 0,
      page: 1,
      pageSize:10,
      sels: [],//选中列表
      editFormVisable: false,//编辑界面是否显示
      editForm:{},
      addFormVisable: false,
      addLoading: false,
      addForm: {},
      loading: false,
      loadingExpand: false,
      adFormVisable: false,
      adForm: {},
      gdForm: {},
      openId: 0,
      selectValue: ''
    }
  },
  created() {
    var This = this
    //adList.limit(10)
    adList.find().then(res => {
      //console.log(res);
      //console.log(res);
      This.allData = res
      //console.log(This.formData[3].ad_gd);
      This.allData.map(n => {
        n.ad_gd  ? n.ad_num = n.ad_gd.length : n.ad_num = 0;
      })
      this.formData = This.allData.slice((this.page-1)*this.pageSize, this.page*this.pageSize)
      this.total = This.allData.length

      //console.log(This.formData);
      // for (var i = 0; i< this.formData.length; i++) {
      //   This.formData[i].ad_time.iso = util.formatDate.format(res[i].ad_time.iso, 'yyyy-mm-dd')
      // }
    });
  },
  methods: {
    sizeChange(val) {

    },
    prevClick(val) {
      this.page = val;
      this.formData = this.allData.slice((this.page-1)*this.pageSize, this.page*this.pageSize)
    },
    nextClick(val) {
      this.page = val;
      this.formData = this.allData.slice((this.page-1)*this.pageSize, this.page*this.pageSize)
    },
    currentChange(val) {
      this.page = val;
      this.formData = this.allData.slice((this.page-1)*this.pageSize, this.page*this.pageSize)
    },
    //新增广告机
    handleAdd() {
      this.addNew = true;
    },
    addNewAdList() {
      this.loading = true
      //console.log(time);
      //adList.set('ad_id',parseInt(this.addNewList.ad_id))
      console.log(this.addNewList.ad_text);
      this.$confirm('确认提交？','提示',{}).then(() => {
        if (!this.addNewList.ad_text) {
          this.addNewList.ad_text = ''
        }
        adList.set('ad_area', this.addNewList.ad_area)
        adList.set('ad_area_list', this.addNewList.ad_area_list)
        adList.set('ad_class', this.addNewList.ad_class)
        adList.set('ad_num', 0)
        adList.set('ad_text', this.addNewList.ad_text)
        adList.set('ad_gd', [])
        adList.save().then(res => {
          adList.get(res.objectId).then( ires => {
            //console.log(ires);
            this.allData.push(ires)
            this.formData = this.allData.slice((this.page-1)*this.pageSize, this.page*this.pageSize)
            this.total = this.allData.length
            this.addNew = false
            this.loading = false
          })
        }).catch(err => {
          console.log(err);
        })
      })
    },
    //获取广告机列表
    getAdList(row, expandedRows) {
      // nconsole.log(row.ad_gd);
      if (row.ad_gd) {
        this.loadingExpand = true
        gdList.containedIn("gd_id", row.ad_gd)
        gdList.find().then( res => {
          //console.log(res);
          this.adinfo[row.ad_id] = res
          this.loadingExpand = false
          //console.log(this.adinfo);
        })
      }

    },
    //查询
    getSelect() {
      //this.allData
      //console.log(this.allData[0].ad_area);
      // console.log(this.selectValue);
      // for (let i = 0; i < this.allData.length;i++) {
      //   //console.log(this.allData[i]);
      //   if (this.allData[i].ad_araa == this.selectValue) {
      //     console.log(1);
      //   }
      // }
      if ( this.selectValue == '') return
      adList.equalTo('ad_area', "==", this.selectValue)
      adList.find().then(res => {
        this.formData = res
      })

    },
    handleEdit(index, row) {
      this.editFormVisable = true;
      //console.log(row);
      this.editForm = row
      //console.log(this.editForm)
    },
    // adEdit(index, row, id) {
    //   this.adForm=this.formData[id-1].info[index]
    //   this.adFormVisable = true
    // },
    editSubmit() {
      let This = this;
      This.$confirm('确认提交？','提示',{}).then(() => {
        This.loading = true
        This.editFormVisable = false
        adList.get(This.editForm.objectId).then(res => {
          res.set("ad_text",This.editForm.ad_text)
          res.save()
          This.loading = false
          This.$message('修改成功')
        })

        // adList.set('ad_text', row.ad_text)
        // adList.save().then(res => {
        //   This.loading = false
        //   This.$message('修改成功')
        // }).catch(err => {
        //   console.log(err);
        // })

      })
    },
    //删除广告
    gdDellet(index,row) {
      this.$confirm('确认删除？','提示',{}).then(() => {
        row.ad_gd.splice(index,1)
        this.adinfo[row.ad_id].splice(index, 1)
        //console.log(row);
        adList.get(row.objectId).then(res => {
          res.set('ad_gd', row.ad_gd )
          res.save()
          This.$message('修改成功')
        })
      })
    },
    //新增广告
    AdeditSubmit() {
      let This = this;
      This.$confirm('确认提交？','提示',{}).then(() => {
        This.loading = true
        //this.gdForm
        //console.log(This.gdForm);
        gdList.set('sjgd_name',This.gdForm.sjgd_name)
        gdList.set('gd_name',This.gdForm.gd_name)
        gdList.set('gd_time',This.gdForm.gd_time)
        gdList.save().then(res => {
          This.loading = false
          This.adFormVisable = false
          //console.log(res);

          gdList.get(res.objectId).then(cres => {
            //console.log(cres);
            This.adinfo[This.formData[This.openId].ad_id].push(cres)
            //console.log(This.adinfo);
            This.formData[This.openId].ad_gd.push(cres.gd_id)
            let objectId = This.formData[This.openId].objectId
            //console.log(objectId);
          //  console.log();
             //This.formData[This.openId].ad_gd.push(gd_id)
          //
            adList.get(This.formData[This.openId].objectId).then(dres=> {
              dres.set('ad_num',This.formData[This.openId].ad_num + 1 )
              dres.set('ad_gd',This.formData[This.openId].ad_gd )
              dres.save()
              //console.log(dres)
            })
          })
          //console.log(res);
          //This.formData[This.openId].ad_gd.push(res.gd_id)
          // adList.get(This.formData[This.openId].objectId).then(res=> {
          //   adList.set('ad_gd',This.formData[This.openId].ad_gd )
          //   adList.save()
          // }).catch(err => {
          //   console.log(err);
          // })
        })
      })
    },
    addAd(index, row) {
      this.adFormVisable = true;
      this.openId = index
      //console.log(this.openId);
    },
    filterTag(value, row) {
      return row.tag === value
    }
  }
}
</script>

<style lang="css">
.my-table {
  min-height: 640px;
}
.my-table,.my-table::before {
  background-color: #fff;
}
.content-wrapper .toolbar {
  margin: 10px 0;
}
.el-table__expanded-cell[class*=cell] {
  padding: 0;
}
.ad-lunbo-text {
  padding: 20px;
}
</style>



