<template lang="html">
  <section>
  <!--工具条-->
  <el-col :span="24" class="toolbar" style="padding-bottom:0">
    <el-form :inline="true" >
      <el-form-item>
        <el-input v-model="selectValue"  placeholder="商家名称"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="getSelect">查询</el-button>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="handleAdd">新增</el-button>
      </el-form-item>
    </el-form>
  </el-col>

  <el-table :data="sjData" style="width: 100%" class="my-table" v-loading="loading" highlight-current-row @expand-change="getInfo">
    <el-table-column type="expand">
      <template slot-scope="props">
        <el-form label-position="left" inline class="demo-table-expand my-table-expand">
          <el-form-item label="开始日期">
            <span>{{ props.row.sj_starttime }}</span>
          </el-form-item>
          <el-form-item label="结束日期">
            <span>{{ props.row.sj_endtime }}</span>
          </el-form-item>
          <el-form-item label="签约金额">
            <span>{{ props.row.sj_price }}</span>
          </el-form-item>
        </el-form>
      </template>
    </el-table-column>
    <el-table-column prop="sj_name"  label="商家名称"></el-table-column>
    <el-table-column prop="sj_arer"  label="商家地址"></el-table-column>
    <el-table-column prop="sj_connect"  label="联系人"></el-table-column>
    <el-table-column prop="sj_phone"  label="联系电话"></el-table-column>
    <el-table-column prop="sj_class"  label="所属行业"></el-table-column>
    <el-table-column prop="sj_qianyue"  label="是否签约"
      :filters="[{text: '已签约', value: '已签约'},{ text: '未签约', value: '未签约' }]"
      :filter-method="filterTag" filter-placement="bottom-end">
      <template slot-scope="scope">
        <el-tag :type="scope.row.sj_qianyue !== '已签约' ? 'danger' : 'success'">{{ scope.row.sj_qianyue }}</el-tag>
      </template>
    </el-table-column>
  </el-table>

  <!--工具条-->
  <el-col :span="24" class="toolbar" v-show="this.allData.length>10">
    <el-pagination layout="prev, pager, next" background  :page-zise="pageSize" :total="total"
    @prev-click="prevClick" @next-click="nextClick" @current-change="currentChange"
    style="float:right">
    </el-pagination>
  </el-col>

  <!--新增商家-->
  <el-dialog title="新增商家" :visible.sync="addNew">
    <el-form :model="newList" label-width="80px" ref="addNewList">
        <el-form-item label="商家名称">
          <el-input v-model="newList.sj_name"></el-input>
        </el-form-item>
        <el-form-item label="商家地址">
          <el-input v-model="newList.sj_arer"></el-input>
        </el-form-item>
        <el-form-item label="联系人">
          <el-input v-model="newList.sj_connect"></el-input>
        </el-form-item>
        <el-form-item label="联系电话">
          <el-input v-model="newList.sj_phone"></el-input>
        </el-form-item>
        <el-form-item label="所属行业">
          <el-input v-model="newList.sj_class"></el-input>
        </el-form-item>
        <el-form-item label="是否签约">
          <el-select v-model="newList.sj_qianyue" placeholder="请选择">
            <el-option label="已签约" value="已签约"></el-option>
            <el-option label="未签约" value="未签约"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="签约金额">
          <el-input v-model="newList.sj_price"></el-input>
        </el-form-item>
        <!-- <el-form-item label="">
          <el-date-picker v-model="newList.ad_time" type="date" style="width:100%" ></el-date-picker>
        </el-form-item> -->
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button >取消</el-button>
        <el-button type="primary" @click="addNewList" >提交</el-button>
      </div>
  </el-dialog>

  </section>

</template>

<script>
import util from '../../util/utils'
import '../../assets/js/key.js'
const merchant = Bmob.Query('merchant');
export default {
  data() {
    return {
      sjData:[],
      allData:[],
      newList:{},
      addNew: false,
      total: 0,
      page: 1,
      pageSize: 10,
      loading : false,
      selectValue: ''
    }
  },
  created(){
    merchant.find().then(res => {
      this.allData = res
      //console.log(this.allData);
      this.sjData = this.allData.slice((this.page-1)*this.pageSize, this.page*this.pageSize)
      this.total = this.allData.length
    });
  },
  methods: {
    filterTag(value,row) {
      return row.sj_qianyue === value
    },
    prevClick(val) {
      this.page = val;
      this.sjData = this.allData.slice((this.page-1)*this.pageSize, this.page*this.pageSize)
    },
    nextClick(val) {
      this.page = val;
      this.sjData = this.allData.slice((this.page-1)*this.pageSize, this.page*this.pageSize)
    },
    currentChange(val) {
      this.page = val;
      this.sjData  = this.allData.slice((this.page-1)*this.pageSize, this.page*this.pageSize)
    },
    addNewList() {
      this.loading = true
      for (var key in this.newList) {
        merchant.set(key, this.newList[key])
      }
      merchant.save().then(res => {
        console.log(res);
        merchant.get(res.objectId).then(ires => {
          this.allData.push(ires)
          this.sjData = this.allData.slice((this.page-1)*this.pageSize, this.page*this.pageSize)
          this.loading = false
          this.addNew = false
        })


      })
    },
    getSelect() {
      if ( this.selectValue == '') return
      merchant.equalTo('sj_name', "==", this.selectValue)
      merchant.find().then(res => {
        this.sjData = res
      })
    },
    handleAdd() {
      this.addNew = true
    },
    //展开商家信息
    getInfo() {

    }
  }
}
</script>

<style lang="css">
.my-table-expand {
  width: 100%;
  padding: 20px;
  overflow: hidden;
}
.my-table-expand  .el-form-item {
  width: 50%;
  margin: 0;
  float: left;
}
.my-table-expand  .el-form-item label {
  color: #999;
}
</style>



