<template>
  <el-row class="container">
    <el-col :span="24" class="header">
      <el-col :span="10" class="logo" :class="collapsed?'logo-collapse-width':'logo-width'">
        中城传媒
      </el-col>
      <el-col :span="10">
        <div class="tools" @click='collapse'>
          <i class="el-icon-menu"></i>
        </div>
      </el-col>
      <el-col :span="4" class="userinfo">
        <el-dropdown trigger="hover">
          <span class="el-dropdown-link userinfo-inner">
            <img :src="'https://raw.githubusercontent.com/taylorchen709/markdown-images/master/vueadmin/user.png'">test</span>
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item>我的消息</el-dropdown-item>
            <el-dropdown-item>设置</el-dropdown-item>
            <el-dropdown-item>退出登陆</el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
      </el-col>
    </el-col>

    <el-col :span="24" class="main">
      <aside :class="collapsed?'menu-collapse':'menu-expanded'">
          <el-menu :default-active="$route.path" class="el-menu-vertical-demo" @open="handleOpen" router
            @close="handleClose" :collapse="collapsed" :collapse-transition=false v-show="!collapsed">
            <!--导航菜单-->
            <template v-for="(item, index) in menuData" v-if="!item.hidden">
              <el-submenu :index="index+''" v-if="!item.leaf">
                <template slot="title">
                  <i :class="item.iconCls"></i>{{item.name}}
                </template>
                <el-menu-item v-for="child in item.children" :index="child.path" v-if="!child.hidden">
                  {{child.name }}
                </el-menu-item>
              </el-submenu>
              <el-menu-item v-if="item.leaf&&item.children.length>0" :index="item.children[0].path">
                <i :class="item.iconCls"></i>{{item.children[0].name}}
              </el-menu-item>
            </template>
            <!--折叠hou-->
          </el-menu>
          <ul class="el-menu el-menu-vertical-demo collapsed el-menu-collapsed" v-show="collapsed" ref="menuCollapsed">
            <li v-for="(item, index) in menuData" v-if="!item.hidden" class="el-submenu item">
              <template v-if="!item.leaf">
                <div class="el-submenu__title" style="padding-left:20px;"  @mouseover="showMenu(index, true)" @mouseout="showMenu(index,false)">
                  <i :class="item.iconCls"></i>
                </div>
                <ul class="el-submenu submenu el-menu" :class="'submenu-hook-'+index"  @mouseover="showMenu(index, true)" @mouseout="showMenu(index,false)">
                  <li v-for="child in item.children" v-if="!child.hidden" :key="child.path" class="el-menu-item" style="padding-left:40px;color#fff"
                  :class="$route.path==child.path?'is-active':''" @click="$router.push(child.path)">
                    {{child.name}}
                  </li>
                </ul>
              </template>
              <template v-else>
                <li class="el-submenu">
                  <div class="el-submenu__title el-menu-item" style="padding-left:20px;height:56px;line-height:56px;padding:0 20px;"
                  :class="$route.path==item.children[0].path?'is-active':''" @click="$router.push(item.children[0].path)">
                    <i :class="item.iconCls"></i>
                  </div>
                </li>
              </template>
            </li>
          </ul>
      </aside>

      <section class="content-container">

        <div class="grid-content bg-purple-light">
          <el-col :span="24" class="breadcrumb-container">
            <strong class="title">{{ $route.name }}</strong>
          </el-col>
          <el-col :span="24" class="content-wrapper">
            <transition name="fade" mode="out-in">
              <router-view/>
            </transition>
          </el-col>
        </div>
      </section>
    </el-col>

  </el-row>
</template>

<script>
let initMenu = function() {
  for(let i in this.$router.options.routes) {
    let root = this.$router.options.routes[i]
    if (root.hidden)
        continue
    let children = []
    for(let j in root.children) {
      let item = root.children[j]
      if(item.hidden)
        continue
      children.push(item)
    }

    if(children.length < 1)
      continue
    this.menuData.push(root)
    root.children = children
  }
}
	export default {
		data() {
			return {
				sysName:'Home',
				collapsed:false,
				sysUserName: '',
				sysUserAvatar: '',
        menuData: []
			}
		},
    created() {
      this.$router.push({path: '/ad'})
    },
		methods: {
      initMenu: initMenu,
			onSubmit() {
				console.log('submit!');
			},
      handleOpen(key, keyPath) {
        console.log(key,keyPath)
      },
      handleClose(key, keyPath) {
        console.log(key, keyPath)
      },
			//折叠导航栏
			collapse(){
				this.collapsed=!this.collapsed;
			},
			 showMenu(i,status){
			 	this.$refs.menuCollapsed.getElementsByClassName('submenu-hook-'+i)[0].style.display=status?'block':'none';
			 }
	},
  mounted() {
    this.initMenu();
  }
}
</script>

<style scoped lang="">
.container {
  position: absolute;
  top: 0;
  bottom: 0;
  width: 100%;
}
.container .header {
  height: 60px;
  line-height: 60px;
  background: #20a0ff;
  color: #fff;
}
.container .header .logo {
  height: 60px;
  font-size: 22px;
  padding-left: 20px;
  padding-right: 20px;
  border-color: hsla(62, 77%, 76%, 0.3);
  border-right-width: 1px;
  border-right-style: solid;
}
.container .header .logo-width {
  width: 230px;
}
.container .header .logo-collapse-width {
  width: 60px;
}
.container .header .tools {
  padding: 0 23px;
  width: 14px;
  height: 60px;
  line-height: 60px;
  cursor: pointer;
}

.container .header .userinfo {
  text-align: center;
  padding-right: 35px;
  float: right;
}
.userinfo .userinfo-inner {
  cursor: pointer;
  color: #fff;
}
.userinfo .userinfo-inner img {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  margin: 10px 0 10px 10px;
  float: right;
}

.container .main{
  display: flex;
  position: absolute;
  top: 60px;
  bottom: 0;
  overflow: hidden;
}

.main .menu-expanded {
  flex:0 0 230px;
  width: 230px;
}
.main .menu-collapse {
  flex:0 0 60px;
  width: 60px;
}

.main .el-menu {
  height: 100%;
  background-color: #eef1f6;
}

.container .main .submenu {
  position: absolute;
  top: 0;
  left: 60px;
  z-index: 9999;
  height: auto;
  display: none;
}
.el-submenu .el-menu-item {
  width: auto;
  min-width: 0;
}

.content-container {
  flex:1;
  overflow-y: scroll;
  padding: 20px;
}
</style>



